<template>
  <span class="overline">{{ name }}
    <v-badge inline
      v-bind:color="classes[content]"
      v-bind:content="content">
    </v-badge>
  </span>
</template>

<script>
export default {
  name: 'Badge',
  props: {
    name: String,
    content: String,
    color: String,
  },
  data: () => ({
    classes: ['green', 'blue', 'orange', 'red'],
  }),
};
</script>
