<template>
  <v-content>
    <v-container fluid>
      <v-row>
        <transition name="fade">
        <v-col v-show="panel">
          <h1 class="headline">Description</h1>
          <v-textarea style="font-family: monospace;"
          name="input-7-1" label="Spécifications JSON"
          v-model="jsonText">
        </v-textarea>
        <div align="right">
        <v-btn class="ma-2" tile outlined color="warning"
        @click="clear()">
        <v-icon left>mdi-delete</v-icon>Effacer
      </v-btn>
      <v-btn class="ma-2" tile outlined color="primary"
      @click="save()" disabled>
      <v-icon left>mdi-export</v-icon>Exporter
    </v-btn></div>
  </v-col>
  </transition>
  <v-col>
      <div v-if="compiled">
          <div class="d-flex flex-wrap align-content-strech">
        <div v-for="item in obj" :key="item.title">
          <Card :properties="item"/>
        </div>
      </div>
      </div>
      <div v-else>
        <div class="text-center">
          <span class="group pa-2">
            <v-icon v-show="!imgShow" color="orange darken-2" x-large>mdi-alert</v-icon>
          </span>
          <p class="orange--text">{{ compilationErrorMsg }}</p>
          <div align="center">
            <v-img v-show="imgShow" width="450" src="../assets/empty.svg"></v-img>
          </div>
</div>
      </div>
  </v-col>
</v-row>
</v-container>
</v-content>

</template>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>


<script>

import Card from './Card.vue';

export default {
  name: 'Speck',
  components: {
    Card,
  },

  props: {
    panel: Boolean,
  },
  data: () => ({
    // JSON specifications
    jsonText: `{
      "list": [
        {
          "title": "Lorem ipsum dolor sit amet",
          "input": "Placerat interdum",
          "param": "Phasellus commodo dolor ipsum, et hendrerit",
          "output": "Nam laoreet augue vel mattis ultricies",
          "details": "Vestibulum aliquet leo felis, vel semper diam tincidunt et.Suspendisse eu euismod tortor. Mauris faucibus nec sem fringilla fermentum in ultricies enim",
          "priority": "3",
          "complexity": "0",
          "value": "2"
        }
      ]
    }`,

    // Parsed JSON object
    obj: {},

    // Display result if true, or error message otherwise
    compiled: true,

    // Display picture when text is empty
    imgShow: true,

    // Toogle description panel
    showDescriptionPanel: true,

    // Error msg
    compilationErrorMsg: '',
  }),
  watch: {
    // Whenever specifications change, this function will run
    jsonText() {
      try {
        // Parse JSON text...
        this.obj = JSON.parse(this.jsonText).list;

        // ...and display result if there's no syntax error
        this.compiled = true;
        this.imgShow = false;
      } catch (error) {
        if (this.jsonText.replace(/\s/g, '').length > 0) {
          // Otherwise, print the error message
          this.compiled = false;
          this.imgShow = false;
          this.compilationErrorMsg = error.message;
        } else {
          this.compiled = false;
          this.imgShow = true;
          this.compilationErrorMsg = '';
        }
      }
    },
  },
  mounted() {
    this.obj = JSON.parse(this.jsonText).list;
  },
  methods: {
    // Removes JSON text
    clear() {
      this.jsonText = '';
    },
  },
};
</script>
